import {
  QUERY_LATEST_SHOP,
  NEW_LATEST_SHOP,
  UPDATE_LATEST_SHOP,
  DELETE_LATEST_SHOP,
  CHANGE_LATEST_SHOP_SAVE_LOADING,
  CHANGE_LATEST_SHOP_DELETE_LOADING,
  UPDATE_FOCUSED_LATEST_SHOP,
  REPLACE_FOCUSED_LATEST_SHOP,
  CHANGE_LATEST_SHOP_FIELD_ERROR,
  LOAD_MORE_LATEST_SHOP,
} from './types';
import { queryLatests as apiQuery, postLatest, patchLatest, deleteLatest as apiDelete } from '../util/latestApi';

export const queryLatestShop = ({ count, max, since }) => dispatch => {

  apiQuery("latest_shop", { count: count, max: max, since: since }, (err, latestShop, meta) => {
    dispatch({
      type: QUERY_LATEST_SHOP,
      payload: latestShop,
      meta: meta,
      error: err,
    });
  });
};

export const loadMoreLatestShop = ({ count: count, max: max, since: since }) => dispatch => {

  return new Promise((resolve, reject) => {

    apiQuery("latest_shop", { count: count, max: max, since: since }, (err, latestShop, meta) => {
      dispatch({
        type: LOAD_MORE_LATEST_SHOP,
        payload: latestShop,
        meta: meta,
        error: err,
      });

      if (err) {
        reject(err);
      } else {
        resolve();
      }
    });
  });
};

export const createLatestShop = (latestShop) => dispatch => {

  dispatch(setSaveLoading(true));

  postLatest("latest_shop", latestShop, (err, createdLatestShop) => {
    dispatch({
      type: NEW_LATEST_SHOP,
      payload: createdLatestShop,
      error: err,
    });
    dispatch(setSaveLoading(false));
  });
};

export const updateLatestShop = (latestShop) => dispatch => {

  dispatch(setSaveLoading(true));

  patchLatest("latest_shop", latestShop, (err, updatedLatestShop) => {
    dispatch({
      type: UPDATE_LATEST_SHOP,
      payload: updatedLatestShop,
      error: err,
    });
    dispatch(setSaveLoading(false));
  });
};

export const deleteLatestShop = (latestShop) => dispatch => {

  dispatch(setDeleteLoading(true));

  apiDelete("latest_shop", latestShop, (err, deletedDetail) => {
    dispatch({
      type: DELETE_LATEST_SHOP,
      payload: deletedDetail,
      error: err,
    });
    dispatch(setDeleteLoading(false));
  });
};

export const updateFocused = ({ id, value }) => dispatch => {

  dispatch({
    type: UPDATE_FOCUSED_LATEST_SHOP,
    payload: { id: id, value: value },
  });
};

export const replaceFocused = (latestShop) => dispatch => {

  dispatch({
    type: REPLACE_FOCUSED_LATEST_SHOP,
    payload: latestShop,
  });
};

export const changeFieldError = ({ id, slug, title, imageURL, shortContent, fullContent }) => dispatch => {

  dispatch({
    type: CHANGE_LATEST_SHOP_FIELD_ERROR,
    payload: {
      id: id,
      slug: slug,
      title: title,
      imageURL: imageURL,
      shortContent: shortContent,
      fullContent: fullContent,
    },
  });
};

const setSaveLoading = (isLoading) => dispatch => {

  dispatch({
    type: CHANGE_LATEST_SHOP_SAVE_LOADING,
    payload: isLoading,
  });
};

const setDeleteLoading = (isLoading) => dispatch => {

  dispatch({
    type: CHANGE_LATEST_SHOP_DELETE_LOADING,
    payload: isLoading,
  });
};