import { CHANGE_MENU, CHANGE_CONFIRMATION_SHOWN } from '../actions/types';

const initialState = {
  openedMenu: 0,
  isConfirmationShown: false,
  confirmationDescription: "",
  onConfirmationYes: () => { },
  onConfirmationNo: () => { },
}

export default function (state = initialState, action) {

  switch (action.type) {
    case CHANGE_MENU:
      return {
        ...state,
        openedMenu: action.payload,
      }
    case CHANGE_CONFIRMATION_SHOWN:
      return {
        ...state,
        isConfirmationShown: action.payload.isShown,
        confirmationDescription: action.payload.isShown ? action.payload.description : "",
        onConfirmationYes: action.payload.isShown ? action.payload.onYes : () => { },
        onConfirmationNo: action.payload.isShown ? action.payload.onNo : () => { },
      }
    default:
      return state;
  }
}