import Constants from './constants';
import { mapObject, JSON_TO_LATEST, LATEST_TO_JSON } from '../util';

export const queryLatests = (type, { count, max, since }, callback) => {
  fetch(`${Constants.BASE_URL}/v1/${type}?${count ? `count=${count}` : ""}${max ? `&max=${max}` : ""}${since ? `&since=${since}` : ""}`)
    .then((response) => {

      if (response.status < 200 || response.status >= 300) {
        console.error(`Fetch returned with code ${response.status} with message <${response.statusText}>`);

        callback(`Fetch returned with code ${response.status} with message <${response.statusText}>`);
        return;
      }

      response.json().then((data) => {

        const latests = data.map((item) => {
          return mapObject(item, JSON_TO_LATEST);
        });

        callback(null, latests, { totalCount: parseInt(response.headers.get("Total-Count")) });
      })
        .catch((err) => {

          console.error(`Fetch error in LatestsApi in queryLatests in response.json(): ${err}`);
          callback(`Fetch error in LatestsApi in queryLatests in response.json(): ${err}`);
        });
    })
    .catch((err) => {
      console.error(`Fetch error in LatestsApi in queryLatests in fetch(...): ${err}`);
      callback(`Fetch error in LatestsApi in queryLatests in fetch(...): ${err}`);
    });
};

export const postLatest = (type, latest, callback) => {
  fetch(`${Constants.BASE_URL}/v1/${type}`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(mapObject(latest, LATEST_TO_JSON)),
  })
    .then((response) => {
      if (response.status < 200 || response.status >= 300) {
        console.error(`POST <${type}> returned with code ${response.status} with message <${response.statusText}>`);
        callback(`POST <${type}> returned with code ${response.status} with message <${response.statusText}>`);
        return;
      }

      response.json().then((data) => {

        callback(null, mapObject(data, JSON_TO_LATEST));
      })
        .catch((err) => {

          console.error(`POST error in LatestsApi in postLatest in response.json(): ${err}`);
          callback(`POST error in LatestsApi in postLatest in response.json(): ${err}`);
        });
    })
    .catch((err) => {

      console.error(`POST error in LatestsApi in postLatest in fetch(...): ${err}`);
      callback(`POST error in LatestsApi in postLatest in fetch(...): ${err}`);
    });
};

export const patchLatest = (type, latest, callback) => {

  fetch(`${Constants.BASE_URL}/v1/${type}/${latest.id}`, {
    method: "PATCH",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(mapObject(latest, LATEST_TO_JSON)),
  })
    .then((response) => {
      if (response.status < 200 || response.status >= 300) {
        console.error(`PATCH <${type}> returned with code ${response.status} with message <${response.statusText}>`);
        callback(`PATCH <${type}> returned with code ${response.status} with message <${response.statusText}>`);
        return;
      }

      response.json().then((data) => {

        callback(null, mapObject(data, JSON_TO_LATEST));
      })
        .catch((err) => {

          console.error(`PATCH error in LatestsApi in patchLatest in response.json(): ${err}`);
          callback(`PATCH error in LatestsApi in patchLatest in response.json(): ${err}`);
        });
    })
    .catch((err) => {

      console.error(`PATCH error in LatestsApi in patchLatest in fetch(...): ${err}`);
      callback(`PATCH error in LatestsApi in patchLatest in fetch(...): ${err}`);
    });
};

export const deleteLatest = (type, latest, callback) => {

  fetch(`${Constants.BASE_URL}/v1/${type}/${latest.id}`, {
    method: "DELETE",
  })
    .then((response) => {
      if (response.status < 200 || response.status >= 300) {
        console.error(`DELETE <${type}> returned with code ${response.status} with message <${response.statusText}>`);
        callback(`DELETE <${type}> returned with code ${response.status} with message <${response.statusText}>`);
        return;
      }

      response.json().then((data) => {

        callback(null, data);

      }).catch((err) => {

        console.error(`DELETE error in LatestsApi in deleteLatest in response.json(): ${err}`);
        callback(`DELETE error in LatestsApi in deleteLatest in response.json(): ${err}`);
      });
    })
    .catch((err) => {

      console.error(`DELETE error in LatestsApi in deleteLatest in fetch(...): ${err}`);
      callback(`DELETE error in LatestsApi in deleteLatest in fetch(...): ${err}`);
    });
};