import {
  QUERY_ADS,
  NEW_AD,
  UPDATE_AD,
  DELETE_AD,
  CHANGE_AD_SAVE_LOADING,
  CHANGE_AD_DELETE_LOADING,
  UPDATE_FOCUSED_AD,
  REPLACE_FOCUSED_AD,
  CHANGE_AD_FIELD_ERROR,
  LOAD_MORE_ADS,
} from '../actions/types';
import slugify from 'slugify';

const initialState = {
  focusedContent: "",
  focusedID: "",
  focusedSlug: "",
  focusedTitle: "",
  focusedImageURL: "",
  isContentError: false,
  isIDError: false,
  isLoadingDelete: false,
  isLoadingSave: false,
  isSlugError: false,
  isTitleError: false,
  isImageURLError: false,
  items: [],
  totalCount: 0,
};

function queryAds(state, action) {
  
  return {
    ...state,
    items: [...action.payload],
    totalCount: action.meta.totalCount,
  };
}

function updateFocusedAd(state, action) {
  let newFocused = {};
  newFocused[action.payload.id] = action.payload.value;

  // Create slug from title
  if (action.payload.id === "focusedTitle") {
    newFocused["focusedSlug"] = slugify(action.payload.value, { lower: true, remove: /[*+~.()'"!:@]/g });
  }

  return {
    ...state,
    ...newFocused,
  };
}

function newAd(state, action) {

  // TODO: proper total count from meta

  return {
    ...state,
    items: [action.payload, ...state.items],
    totalCount: state.totalCount + 1,
    focusedContent: action.payload.content,
    focusedID: action.payload.id,
    focusedSlug: action.payload.slug,
    focusedTitle: action.payload.title,
    focusedImageURL: action.payload.imageURL,
  };
}

function deleteAd(state, action) {

  // TODO: proper total count from meta

  return {
    ...state,
    items: state.items.filter(item => item.id !== action.payload.deletedID),
    totalCount: state.totalCount - 1,
    focusedContent: "",
    focusedID: "",
    focusedSlug: "",
    focusedTitle: "",
    focusedImageURL: "",
  };
}

function updateAd(state, action) {

  return {
    ...state,
    items: state.items.map(item => {

      if (item.id === action.payload.id) {
        return {
          ...action.payload
        }
      } else {
        return { ...item };
      }
    }),
    focusedContent: action.payload.content,
    focusedID: action.payload.id,
    focusedSlug: action.payload.slug,
    focusedTitle: action.payload.title,
    focusedImageURL: action.payload.imageURL,
  };
}

function loadMoreAds(state, action) {

  action.payload.shift();

  return {
    ...state,
    items: [...state.items, ...action.payload],
    totalCount: action.meta.totalCount,
  };
}

export default function (state = initialState, action) {

  if (action.error) {
    console.error(`[ERROR] AdReducer error in ${action.type}: ${action.error}`);
    return state;
  }
  
  switch (action.type) {
    case QUERY_ADS:
      return queryAds(state, action);
    case CHANGE_AD_SAVE_LOADING:
      return {
        ...state,
        isLoadingSave: action.payload,
      };
    case CHANGE_AD_DELETE_LOADING:      
      return {
        ...state,
        isLoadingDelete: action.payload,
      };
    case UPDATE_FOCUSED_AD:
      return updateFocusedAd(state, action);
    case REPLACE_FOCUSED_AD:
      return {
        ...state,
        focusedID: action.payload.id,
        focusedSlug: action.payload.slug,
        focusedTitle: action.payload.title,
        focusedImageURL: action.payload.imageURL,
        focusedContent: action.payload.content,
      };
    case CHANGE_AD_FIELD_ERROR:
      return {
        ...state,
        isContentError: action.payload.content,
        isIDError: action.payload.id,
        isSlugError: action.payload.slug,
        isImageURLError: action.payload.imageURL,
        isTitleError: action.payload.title,
      };
    case NEW_AD:
      return newAd(state, action);
    case DELETE_AD:
      return deleteAd(state, action);
    case UPDATE_AD:
      return updateAd(state, action);
    case LOAD_MORE_ADS:
      return loadMoreAds(state, action);
    default:
      return state;
  }
}