import {
  QUERY_LATEST_NEWS,
  NEW_LATEST_NEWS,
  UPDATE_LATEST_NEWS,
  DELETE_LATEST_NEWS,
  CHANGE_LATEST_NEWS_SAVE_LOADING,
  CHANGE_LATEST_NEWS_DELETE_LOADING,
  UPDATE_FOCUSED_LATEST_NEWS,
  REPLACE_FOCUSED_LATEST_NEWS,
  CHANGE_LATEST_NEWS_FIELD_ERROR,
  LOAD_MORE_LATEST_NEWS,
} from './types';
import { queryLatests as apiQuery, postLatest, patchLatest, deleteLatest as apiDelete } from '../util/latestApi';

export const queryLatestNews = ({ count, max, since }) => dispatch => {

  apiQuery("latest_news", { count: count, max: max, since: since }, (err, latestNews, meta) => {
    dispatch({
      type: QUERY_LATEST_NEWS,
      payload: latestNews,
      meta: meta,
      error: err,
    });
  });
};

export const loadMoreLatestNews = ({ count: count, max: max, since: since }) => dispatch => {

  return new Promise((resolve, reject) => {

    apiQuery("latest_news", { count: count, max: max, since: since }, (err, latestNews, meta) => {
      dispatch({
        type: LOAD_MORE_LATEST_NEWS,
        payload: latestNews,
        meta: meta,
        error: err,
      });

      if (err) {
        reject(err);
      } else {
        resolve();
      }
    });
  });
};

export const createLatestNews = (latestNews) => dispatch => {

  dispatch(setSaveLoading(true));

  postLatest("latest_news", latestNews, (err, createdLatestNews) => {
    dispatch({
      type: NEW_LATEST_NEWS,
      payload: createdLatestNews,
      error: err,
    });
    dispatch(setSaveLoading(false));
  });
};

export const updateLatestNews = (latestNews) => dispatch => {

  dispatch(setSaveLoading(true));

  patchLatest("latest_news", latestNews, (err, updatedLatestNews) => {
    dispatch({
      type: UPDATE_LATEST_NEWS,
      payload: updatedLatestNews,
      error: err,
    });
    dispatch(setSaveLoading(false));
  });
};

export const deleteLatestNews = (latestNews) => dispatch => {

  dispatch(setDeleteLoading(true));

  apiDelete("latest_news", latestNews, (err, deletedDetail) => {
    dispatch({
      type: DELETE_LATEST_NEWS,
      payload: deletedDetail,
      error: err,
    });
    dispatch(setDeleteLoading(false));
  });
};

export const updateFocused = ({ id, value }) => dispatch => {

  dispatch({
    type: UPDATE_FOCUSED_LATEST_NEWS,
    payload: { id: id, value: value },
  });
};

export const replaceFocused = (latestNews) => dispatch => {

  dispatch({
    type: REPLACE_FOCUSED_LATEST_NEWS,
    payload: latestNews,
  });
};

export const changeFieldError = ({ id, slug, title, imageURL, shortContent, fullContent }) => dispatch => {

  dispatch({
    type: CHANGE_LATEST_NEWS_FIELD_ERROR,
    payload: {
      id: id,
      slug: slug,
      title: title,
      imageURL: imageURL,
      shortContent: shortContent,
      fullContent: fullContent,
    },
  });
};

const setSaveLoading = (isLoading) => dispatch => {

  dispatch({
    type: CHANGE_LATEST_NEWS_SAVE_LOADING,
    payload: isLoading,
  });
};

const setDeleteLoading = (isLoading) => dispatch => {

  dispatch({
    type: CHANGE_LATEST_NEWS_DELETE_LOADING,
    payload: isLoading,
  });
};