import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { queryLatestShop, updateFocused, replaceFocused, changeFieldError, updateLatestShop, createLatestShop, deleteLatestShop, loadMoreLatestShop } from '../actions/latestShopActions';
import { changeConfirmationShown } from '../actions/appViewActions';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent'
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import MuiVirtualizedTable from './muiVirtualizedTable';
import InputBase from '@material-ui/core/InputBase';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from '@material-ui/icons/Search';
import DirectionsIcon from '@material-ui/icons/Directions';
import Typography from '@material-ui/core/Typography';
import SaveIcon from '@material-ui/icons/SaveRounded';
import DeleteIcon from '@material-ui/icons/DeleteRounded';
import CircularProgress from '@material-ui/core/CircularProgress';
import Link from '@material-ui/core/Link';

const styles = theme => ({
  root: {
    height: '100%',
  },
  card: {
    padding: 0,
    color: theme.palette.text.secondary,
  },
  tableToolbar: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
  },
  tableToolbarInput: {
    marginLeft: 8,
    flex: 1,
  },
  tableToolbarIconButton: {
    padding: 10,
  },
  tableToolbarDivider: {
    width: 1,
    height: 28,
    margin: 4,
  },
  formContainer: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    marginTop: 19,
  },
  monospaced: {
    fontFamily: 'monospace',
  },
  iconSmall: {
    fontSize: 20,
  },
  button: {
    margin: theme.spacing.unit,
  },
  leftIcon: {
    marginRight: theme.spacing.unit,
  },
  formContent: {
    padding: '8px',
  },
  formAction: {
    padding: '8px',
  },
  formFieldCaption: {
    marginLeft: theme.spacing.unit,
  }
});

class LatestShop extends Component {
  constructor(props) {
    super(props);
  };

  componentDidMount() {
    this.props.queryLatestShop({ count: 2 });
  }

  isRowLoaded = ({ index }) => {
    
    return !!this.props.latestShop[index];
  }

  loadMoreRows = ({ startIndex, stopIndex }) => {
    
    const count = (stopIndex - startIndex) + 2;
    const max = this.props.latestShop[startIndex - 1].id;

    return this.props.loadMoreLatestShop({ count: count, max: max });
  }

  handleOnSaveClick = () => {

    if (
      this.props.focusedSlug === "" ||
      this.props.focusedTitle === "" ||
      this.props.focusedImageURL === "" ||
      this.props.focusedShortContent === "" || 
      this.props.focusedFullContent === "")
    {
      this.props.changeFieldError({
        slug: this.props.focusedSlug === "",
        title: this.props.focusedTitle === "",
        imageURL: this.props.focusedImageURL === "",
        shortContent: this.props.focusedShortContent === "",
        fullContent: this.props.focusedFullContent === "",
      });

      setTimeout(() => {
        this.props.changeFieldError({
          slug: false,
          title: false,
          imageURL: false,
          shortContent: false,
          fullContent: false,
        });
      }, 2000);

      return;
    }
    
    if (this.props.focusedID === "") {
      this.props.createLatestShop({
        slug: this.props.focusedSlug,
        title: this.props.focusedTitle,
        imageURL: this.props.focusedImageURL,
        shortContent: this.props.focusedShortContent,
        fullContent: this.props.focusedFullContent,
      });
    } else {
      this.props.updateLatestShop({
        id: this.props.focusedID,
        slug: this.props.focusedSlug,
        title: this.props.focusedTitle,
        imageURL: this.props.focusedImageURL,
        shortContent: this.props.focusedShortContent,
        fullContent: this.props.focusedFullContent,
      });
    }
  }

  handleOnDeleteClick = () => {

    if (this.props.focusedID === "" ||
      this.props.focusedSlug === "" ||
      this.props.focusedTitle === "" ||
      this.props.focusedShortContent === "" ||
      this.props.focusedFullContent === "") {

      this.props.changeFieldError({
        id: this.props.focusedID === "",
        slug: this.props.focusedSlug === "",
        title: this.props.focusedTitle === "",
        imageURL: this.props.focusedImageURL === "",
        shortContent: this.props.focusedShortContent === "",
        fullContent: this.props.focusedFullContent === "",
      });

      setTimeout(() => {
        this.props.changeFieldError({
          id: false,
          slug: false,
          title: false,
          imageURL: false,
          shortContent: false,
          fullContent: false,
        });
      }, 2000);

      return;
    }

    this.props.changeConfirmationShown({
      isShown: true,
      description: `You are about to delete ${this.props.focusedTitle}`,
      onYes: () => {
        this.props.deleteLatestShop({
          id: this.props.focusedID,
          slug: this.props.focusedSlug,
          title: this.props.focusedTitle,
          shortContent: this.props.focusedShortContent,
          fullContent: this.props.focusedFullContent,
        });
        this.props.changeConfirmationShown({ isShown: false });
      },
      onNo: () => {
        this.props.changeConfirmationShown({ isShown: false });
      },
    })
  }

  render() { 
    const { classes } = this.props;

    return ( 
      <Grid className={classes.root} container spacing={8} alignItems="stretch">
        <Grid item xs={6}>
          <Card className={classes.card} style={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
            <CardHeader
              className={classes.toolbar}
              title={<Paper className={classes.tableToolbar} elevation={0}>
                <SearchIcon />
                <InputBase className={classes.tableToolbarInput} placeholder="Search" />
                <Divider className={classes.divider} />
              </Paper>}
            />
            <CardContent style={{ height: '100%', width: '100%' }}>
              <MuiVirtualizedTable
                style={{ height: '100%', width: '100%' }}
                rowCount={this.props.latestShop.length}
                rowGetter={({ index }) => this.props.latestShop[index]}
                onRowClick={({ event, index, rowData }) => this.props.replaceFocused(rowData)}
                isRowLoaded={this.isRowLoaded}
                loadMoreRows={this.loadMoreRows}
                remoteRowCount={this.props.totalCount}
                columns={[
                  {
                    width: 180,
                    label: 'ID',
                    dataKey: 'id',
                  },
                  {
                    width: 200,
                    label: 'Slug',
                    dataKey: 'slug',
                    flexGrow: 1.0,
                  },
                  {
                    width: 200,
                    label: 'Title',
                    dataKey: 'title',
                    flexGrow: 1.0,
                  },
                ]}
              />
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={6}>
          <Card className={classes.card}>
            <CardContent className={classes.formContent}>
              <form className={classes.formContainer} noValidate autoComplete="off">
                <TextField
                  className={classes.textField}
                  error={this.props.isIDError}
                  fullWidth
                  id="focusedID"
                  label="ID"
                  margin="normal"
                  value={this.props.focusedID}
                  InputProps={{
                    classes: { input: classes.monospaced },
                    readOnly: true,
                  }}
                />
                <TextField
                  className={classes.textField}
                  error={this.props.isSlugError}
                  fullWidth
                  id="focusedSlug"
                  label="Slug"
                  margin="normal"
                  value={this.props.focusedSlug}
                  InputProps={{
                    readOnly: true,
                    classes: { input: classes.monospaced },
                  }}
                />
                <TextField
                  className={classes.textField}
                  error={this.props.isTitleError}
                  InputProps={{
                    classes: { input: classes.monospaced },
                  }}
                  fullWidth
                  id="focusedTitle"
                  label="Title"
                  margin="normal"
                  onChange={(event) => this.props.updateFocused({ id: event.target.id, value: event.target.value })}
                  value={this.props.focusedTitle}
                />
                <TextField
                  className={classes.textField}
                  error={this.props.isImageURLError}
                  InputProps={{
                    classes: { input: classes.monospaced },
                  }}
                  fullWidth
                  id="focusedImageURL"
                  label="Image URL"
                  margin="normal"
                  onChange={(event) => this.props.updateFocused({ id: event.target.id, value: event.target.value })}
                  value={this.props.focusedImageURL}
                />
                <TextField
                  className={classes.textField}
                  error={this.props.isShortContentError}
                  InputProps={{
                    classes: { input: classes.monospaced },
                  }}
                  fullWidth
                  id="focusedShortContent"
                  label="Short Content"
                  margin="normal"
                  multiline
                  onChange={(event) => this.props.updateFocused({ id: event.target.id, value: event.target.value })}
                  value={this.props.focusedShortContent}
                />
                <TextField
                  className={classes.textField}
                  error={this.props.isFullContentError}
                  InputProps={{
                    classes: { input: classes.monospaced },
                  }}
                  fullWidth
                  id="focusedFullContent"
                  label="Full Content"
                  margin="normal"
                  multiline
                  onChange={(event) => this.props.updateFocused({ id: event.target.id, value: event.target.value })}
                  value={this.props.focusedFullContent}
                />
                <Typography className={classes.formFieldCaption} variant="caption">
                  Uses markdown format, use <Link href={"https://jbt.github.io/markdown-editor/"} target="_blank" rel="noreferrer" color="primary"><b>this</b></Link> tool
                </Typography>
              </form>
            </CardContent>
            <CardActions className={classes.formAction}>
              <Button variant="contained" size="small" className={classes.button} color="primary" onClick={this.handleOnSaveClick}>
                {this.props.isLoadingSave ? <CircularProgress className={classes.leftIcon} color="secondary" size={20} /> : <SaveIcon className={classNames(classes.leftIcon, classes.iconSmall)} />}
                Save
              </Button>
              <Button variant="text" size="small" className={classes.button} color="primary" onClick={this.handleOnDeleteClick}>
                {this.props.isLoadingDelete ? <CircularProgress className={classes.leftIcon} color="primary" size={20} /> : <DeleteIcon className={classNames(classes.leftIcon, classes.iconSmall)} />}
                Delete
              </Button>
            </CardActions>
          </Card>
        </Grid>
      </Grid>
    );
  }
}

LatestShop.propTypes = {
  changeConfirmationShown: PropTypes.func.isRequired,
  changeFieldError: PropTypes.func.isRequired,
  createLatestShop: PropTypes.func.isRequired,
  deleteLatestShop: PropTypes.func.isRequired,
  focusedShortContent: PropTypes.string.isRequired,
  focusedFullContent: PropTypes.string.isRequired,
  focusedID: PropTypes.string.isRequired,
  focusedImageURL: PropTypes.string.isRequired,
  focusedSlug: PropTypes.string.isRequired,
  focusedTitle: PropTypes.string.isRequired,
  isShortContentError: PropTypes.bool.isRequired,
  isFullContentError: PropTypes.bool.isRequired,
  isIDError: PropTypes.bool.isRequired,
  isImageURLError: PropTypes.bool.isRequired, 
  isLoadingDelete: PropTypes.bool.isRequired,
  isLoadingSave: PropTypes.bool.isRequired,
  isSlugError: PropTypes.bool.isRequired,
  isTitleError: PropTypes.bool.isRequired,
  latestShop: PropTypes.array.isRequired,
  loadMoreLatestShop: PropTypes.func.isRequired,
  queryLatestShop: PropTypes.func.isRequired,
  replaceFocused: PropTypes.func.isRequired,
  totalCount: PropTypes.number.isRequired,
  updateFocused: PropTypes.func.isRequired,
  updateLatestShop: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  focusedShortContent: state.latestShop.focusedShortContent,
  focusedFullContent: state.latestShop.focusedFullContent,
  focusedID: state.latestShop.focusedID,
  focusedImageURL: state.latestShop.focusedImageURL,
  focusedSlug: state.latestShop.focusedSlug,
  focusedTitle: state.latestShop.focusedTitle,
  isShortContentError: state.latestShop.isShortContentError,
  isFullContentError: state.latestShop.isFullContentError,
  isIDError: state.latestShop.isIDError,
  isImageURLError: state.latestShop.isImageURLError,
  isLoadingDelete: state.latestShop.isLoadingDelete,
  isLoadingSave: state.latestShop.isLoadingSave,
  isSlugError: state.latestShop.isSlugError,
  isTitleError: state.latestShop.isTitleError,
  latestShop: state.latestShop.items,
  totalCount: state.latestShop.totalCount,
});

export default withStyles(styles, { withTheme: true })(connect(mapStateToProps, { queryLatestShop, updateFocused, replaceFocused, changeFieldError, createLatestShop, updateLatestShop, deleteLatestShop, changeConfirmationShown, loadMoreLatestShop })(LatestShop));