import React, { Component } from "react";
import { withStyles, createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import PropTypes from 'prop-types';
import classNames from "classnames";
import CssBaseline from "@material-ui/core/CssBaseline";
import theme from "./muiTheme";
import MenuIcon from "@material-ui/icons/Menu";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import Ads from "./components/ads";
import LatestNews from "./components/latestNews";
import LatestShop from "./components/latestShop";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/AddRounded";
import ConfirmationAlert from "./components/confirmationAlert";
import slugify from "slugify";
import { connect } from 'react-redux';
import { changeMenu, changeConfirmationShown } from './actions/appViewActions';
import { replaceFocused as replaceFocusedAd } from './actions/adActions';
import { replaceFocused as replaceFocusedLatestNews } from './actions/latestNewsActions';
import { replaceFocused as replaceFocusedLatestShop } from './actions/latestShopActions';

const drawerWidth = 120;

const styles = theme => ({
  root: {
    display: "flex",
  },
  fab: {
    margin: 0,
    top: "auto",
    left: "auto",
    bottom: 20,
    right: 20,
    position: "fixed",
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: "0 8px",
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),

  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 36,
  },
  menuButtonHidden: {
    display: "none",
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: "relative",
    whiteSpace: "nowrap",
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: "hidden",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing.unit * 7,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing.unit * 9,
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit,
    height: "100vh",
    overflow: "auto",
  },
  h5: {
    marginBottom: theme.spacing.unit
  },
});

class App extends Component {
  constructor(props) {
    super(props);
  };

  handleOnAddClick = () => {
    switch (this.props.openedMenu) {
      case 0:
        this.props.replaceFocusedAd({ id: "", slug: "", title: "", content: "", imageURL: "" });
        break;
      case 1:
        this.props.replaceFocusedLatestNews({ id: "", slug: "", title: "", shortContent: "", fullContent: "", imageURL: "" });
        break;
      case 2:
        this.props.replaceFocusedLatestShop({ id: "", slug: "", title: "", shortContent: "", fullContent: "", imageURL: "" });
        break;
    }
  }

  render() {

    const { classes } = this.props;

    return (
      <MuiThemeProvider theme={theme}>
        <div className={classes.root} >
          <CssBaseline />
          <Drawer
            variant="permanent"
            classes={{
              paper: classNames(classes.drawerPaper),
            }}
          >
            <List dense={true}>
              <ListItem button onClick={(event) => this.props.changeMenu(0)}>
                <ListItemText primary={"Ads"} />
              </ListItem>
              <ListItem button onClick={(event) => this.props.changeMenu(1)}>
                <ListItemText primary={"Latest News"} />
              </ListItem>
              <ListItem button onClick={(event) => this.props.changeMenu(2)}>
                <ListItemText primary={"Latest Shop"} />
              </ListItem>
              <ListItem button onClick={(event) => this.props.changeMenu(3)}>
                <ListItemText primary={"Shops"} />
              </ListItem>
            </List>
          </Drawer>
          <main className={classes.content}>
            {this.renderMenu()}
          </main>
          <Fab className={classes.fab} color="secondary" aria-label="Add"
            onClick={this.handleOnAddClick}
          >
            <AddIcon />
          </Fab>
          <ConfirmationAlert
            confirmationDescription={this.props.confirmationDescription}
            isOpen={this.props.isConfirmationShown}
            onClose={() => this.props.changeConfirmationShown({ isShown: false })}
            onNo={this.props.onConfirmationNo}
            onYes={this.props.onConfirmationYes}
          />
        </div>
      </MuiThemeProvider>
    );
  }

  renderMenu() {
    switch (this.props.openedMenu) {
      case 0:
        return <Ads />;
      case 1:
        return <LatestNews/>;
      case 2:
        return <LatestShop />;
      case 3:
        return <div />;
    }
  }
}

App.propTypes = {
  changeMenu: PropTypes.func.isRequired,
  openedMenu: PropTypes.number.isRequired,
  isConfirmationShown: PropTypes.bool.isRequired,
  onConfirmationYes: PropTypes.func.isRequired,
  onConfirmationNo: PropTypes.func.isRequired,
  changeConfirmationShown: PropTypes.func.isRequired,
  confirmationDescription: PropTypes.string.isRequired,
  replaceFocused: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  openedMenu: state.appView.openedMenu,
  isConfirmationShown: state.appView.isConfirmationShown,
  onConfirmationYes: state.appView.onConfirmationYes,
  onConfirmationNo: state.appView.onConfirmationNo,
  confirmationDescription: state.appView.confirmationDescription
});

export default withStyles(styles, { withTheme: true })(connect(mapStateToProps, { changeMenu, changeConfirmationShown, replaceFocusedAd, replaceFocusedLatestNews, replaceFocusedLatestShop })(App));
