import {
  QUERY_ADS, NEW_AD,
  UPDATE_AD, DELETE_AD,
  CHANGE_AD_SAVE_LOADING,
  CHANGE_AD_DELETE_LOADING,
  UPDATE_FOCUSED_AD,
  REPLACE_FOCUSED_AD,
  CHANGE_AD_FIELD_ERROR,
  LOAD_MORE_ADS,
} from './types';
import { queryAds as apiQuery, postAd as apiPost, patchAd as apiPatch, deleteAd as apiDelete } from '../util/adsApi';

export const queryAds = ({count, max, since}) => dispatch => {
  
  apiQuery({ count: count, max: max, since: since }, (err, ads, meta) => {
    dispatch({
      type: QUERY_ADS,
      payload: ads,
      meta: meta,
      error: err,
    });
  });
};

export const loadMoreAds = ({ count: count, max: max, since: since }) => dispatch => {
  
  return new Promise((resolve, reject) => {

    apiQuery({ count: count, max: max, since: since }, (err, ads, meta) => {
      dispatch({
        type: LOAD_MORE_ADS,
        payload: ads,
        meta: meta,
        error: err,
      });

      if (err) {
        reject(err);
      } else {
        resolve();
      }
    });
  });
};

export const createAd = (ad) => dispatch => {

  dispatch(setSaveLoading(true));

  apiPost(ad, (err, createdAd) => {
    dispatch({
      type: NEW_AD,
      payload: createdAd,
      error: err,
    });
    dispatch(setSaveLoading(false));
  });
};

export const updateAd = (ad) => dispatch => {

  dispatch(setSaveLoading(true));

  apiPatch(ad, (err, updatedAd) => {
    dispatch({
      type: UPDATE_AD,
      payload: updatedAd,
      error: err,
    });
    dispatch(setSaveLoading(false));
  });
};

export const deleteAd = (ad) => dispatch => {

  dispatch(setDeleteLoading(true));

  apiDelete(ad, (err, deletedDetail) => {
    dispatch({
      type: DELETE_AD,
      payload: deletedDetail,
      error: err,
    });
    dispatch(setDeleteLoading(false));
  });
};

export const updateFocused = ({id, value}) => dispatch => {

  dispatch({
    type: UPDATE_FOCUSED_AD,
    payload: { id: id, value: value },
  });
};

export const replaceFocused = (ad) => dispatch => {

  dispatch({
    type: REPLACE_FOCUSED_AD,
    payload: ad,
  });
};

export const changeFieldError = ({ id, slug, title, imageURL, content }) => dispatch => {
  
  dispatch({
    type: CHANGE_AD_FIELD_ERROR,
    payload: {
      id: id,
      slug: slug,
      title: title,
      imageURL: imageURL,
      content: content,
    },
  });
};

const setSaveLoading = (isLoading) => dispatch => {

  dispatch({
    type: CHANGE_AD_SAVE_LOADING,
    payload: isLoading,
  });
};

const setDeleteLoading = (isLoading) => dispatch => {

  dispatch({
    type: CHANGE_AD_DELETE_LOADING,
    payload: isLoading,
  });
};