import Constants from './constants';
import { mapObject, JSON_TO_AD, AD_TO_JSON } from '../util';

export const queryAds = ({ count, max, since }, callback) => {
  fetch(`${Constants.BASE_URL}/v1/ads?${count ? `count=${count}` : ""}${max ? `&max=${max}` : ""}${since ? `&since=${since}` : ""}`)
    .then((response) => {

      if (response.status < 200 || response.status >= 300) {
        console.error(`Fetch returned with code ${response.status} with message <${response.statusText}>`);

        callback(`Fetch returned with code ${response.status} with message <${response.statusText}>`);
        return;
      }

      response.json().then((data) => {

        const ads = data.map((item) => {
          return mapObject(item, JSON_TO_AD);
        });

        callback(null, ads, { totalCount: parseInt(response.headers.get("Total-Count")) });
      })
        .catch((err) => {

          console.error(`Fetch error in AdsApi in queryAds in response.json(): ${err}`);
          callback(`Fetch error in AdsApi in queryAds in response.json(): ${err}`);
        });
    })
    .catch((err) => {
      console.error(`Fetch error in AdsApi in queryAds in fetch(...): ${err}`);
      callback(`Fetch error in AdsApi in queryAds in fetch(...): ${err}`);
    });
};

export const postAd = (ad, callback) => {
  fetch(`${Constants.BASE_URL}/v1/ads`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(mapObject(ad, AD_TO_JSON)),
  })
    .then((response) => {
      if (response.status < 200 || response.status >= 300) {
        console.error(`POST ad returned with code ${response.status} with message <${response.statusText}>`);
        callback(`POST ad returned with code ${response.status} with message <${response.statusText}>`);
        return;
      }

      response.json().then((data) => {

        callback(null, mapObject(data, JSON_TO_AD));
      })
        .catch((err) => {

          console.error(`POST error in AdsApi in postAd in response.json(): ${err}`);
          callback(`POST error in AdsApi in postAd in response.json(): ${err}`);
        });
    })
    .catch((err) => {
      
      console.error(`POST error in AdsApi in postAd in fetch(...): ${err}`);
      callback(`POST error in AdsApi in postAd in fetch(...): ${err}`);
    });
};

export const patchAd = (ad, callback) => {

  fetch(`${Constants.BASE_URL}/v1/ads/${ad.id}`, {
    method: "PATCH",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(mapObject(ad, AD_TO_JSON)),
  })
    .then((response) => {
      if (response.status < 200 || response.status >= 300) {
        console.error(`PATCH ad returned with code ${response.status} with message <${response.statusText}>`);
        callback(`PATCH ad returned with code ${response.status} with message <${response.statusText}>`);
        return;
      }

      response.json().then((data) => {

        callback(null, mapObject(data, JSON_TO_AD));
      })
        .catch((err) => {

          console.error(`PATCH error in AdsApi in patchAd in response.json(): ${err}`);
          callback(`PATCH error in AdsApi in patchAd in response.json(): ${err}`);
        });
    })
    .catch((err) => {

      console.error(`PATCH error in AdsApi in patchAd in fetch(...): ${err}`);
      callback(`PATCH error in AdsApi in patchAd in fetch(...): ${err}`);
    });
};

export const deleteAd = (ad, callback) => {

  fetch(`${Constants.BASE_URL}/v1/ads/${ad.id}`, {
    method: "DELETE",
  })
    .then((response) => {
      if (response.status < 200 || response.status >= 300) {
        console.error(`DELETE ad returned with code ${response.status} with message <${response.statusText}>`);
        callback(`DELETE ad returned with code ${response.status} with message <${response.statusText}>`);
        return;
      }

      response.json().then((data) => {

        callback(null, data);

      }).catch((err) => {

        console.error(`DELETE error in AdsApi in deleteAd in response.json(): ${err}`);
        callback(`DELETE error in AdsApi in deleteAd in response.json(): ${err}`);
      });
    })
    .catch((err) => {

      console.error(`DELETE error in AdsApi in deleteAd in fetch(...): ${err}`);
      callback(`DELETE error in AdsApi in deleteAd in fetch(...): ${err}`);
    });
};