import {
  QUERY_LATEST_NEWS,
  NEW_LATEST_NEWS,
  UPDATE_LATEST_NEWS,
  DELETE_LATEST_NEWS,
  CHANGE_LATEST_NEWS_SAVE_LOADING,
  CHANGE_LATEST_NEWS_DELETE_LOADING,
  UPDATE_FOCUSED_LATEST_NEWS,
  REPLACE_FOCUSED_LATEST_NEWS,
  CHANGE_LATEST_NEWS_FIELD_ERROR,
  LOAD_MORE_LATEST_NEWS,
} from '../actions/types';
import slugify from 'slugify';

const initialState = {
  focusedFullContent: "",
  focusedShortContent: "",
  focusedID: "",
  focusedSlug: "",
  focusedTitle: "",
  focusedImageURL: "",
  isShortContentError: false,
  isFullContentError: false,
  isIDError: false,
  isLoadingDelete: false,
  isLoadingSave: false,
  isSlugError: false,
  isTitleError: false,
  isImageURLError: false,
  items: [],
  totalCount: 0,
};

function queryLatestNews(state, action) {

  return {
    ...state,
    items: [...action.payload],
    totalCount: action.meta.totalCount,
  };
}

function updateFocusedLatestNews(state, action) {
  let newFocused = {};
  newFocused[action.payload.id] = action.payload.value;

  // Create slug from title
  if (action.payload.id === "focusedTitle") {
    newFocused["focusedSlug"] = slugify(action.payload.value, { lower: true, remove: /[*+~.()'"!:@]/g });
  }

  return {
    ...state,
    ...newFocused,
  };
}

function newLatestNews(state, action) {

  // TODO: proper total count from meta

  return {
    ...state,
    items: [action.payload, ...state.items],
    totalCount: state.totalCount + 1,
    focusedFullContent: action.payload.fullContent,
    focusedID: action.payload.id,
    focusedImageURL: action.payload.imageURL,
    focusedShortContent: action.payload.shortContent,
    focusedSlug: action.payload.slug,
    focusedTitle: action.payload.title,
  };
}

function deleteLatestNews(state, action) {

  // TODO: proper total count from meta

  return {
    ...state,
    items: state.items.filter(item => item.id !== action.payload.deletedID),
    totalCount: state.totalCount - 1,
    focusedShortContent: "",
    focusedFullContent: "",
    focusedID: "",
    focusedSlug: "",
    focusedTitle: "",
    focusedImageURL: "",
  };
}

function updateLatestNews(state, action) {

  return {
    ...state,
    items: state.items.map(item => {

      if (item.id === action.payload.id) {
        return {
          ...action.payload
        }
      } else {
        return { ...item };
      }
    }),
    focusedShortContent: action.payload.shortContent,
    focusedFullContent: action.payload.fullContent,
    focusedID: action.payload.id,
    focusedSlug: action.payload.slug,
    focusedTitle: action.payload.title,
    focusedImageURL: action.payload.imageURL,
  };
}

function loadMoreLatestNews(state, action) {

  action.payload.shift();

  return {
    ...state,
    items: [...state.items, ...action.payload],
    totalCount: action.meta.totalCount,
  };
}

export default function (state = initialState, action) {

  if (action.error) {
    console.error(`[ERROR] LatestNewsReducer error in ${action.type}: ${action.error}`);
    return state;
  }

  switch (action.type) {
    case QUERY_LATEST_NEWS:
      return queryLatestNews(state, action);
    case CHANGE_LATEST_NEWS_SAVE_LOADING:
      return {
        ...state,
        isLoadingSave: action.payload,
      };
    case CHANGE_LATEST_NEWS_DELETE_LOADING:
      return {
        ...state,
        isLoadingDelete: action.payload,
      };
    case UPDATE_FOCUSED_LATEST_NEWS:
      return updateFocusedLatestNews(state, action);
    case REPLACE_FOCUSED_LATEST_NEWS:
      return {
        ...state,
        focusedID: action.payload.id,
        focusedSlug: action.payload.slug,
        focusedTitle: action.payload.title,
        focusedImageURL: action.payload.imageURL,
        focusedShortContent: action.payload.shortContent,
        focusedFullContent: action.payload.fullContent,
      };
    case CHANGE_LATEST_NEWS_FIELD_ERROR:
      return {
        ...state,
        isShortContentError: action.payload.shortContent,
        isFullContentError: action.payload.fullContent,
        isIDError: action.payload.id,
        isSlugError: action.payload.slug,
        isImageURLError: action.payload.imageURL,
        isTitleError: action.payload.title,
      };
    case NEW_LATEST_NEWS:
      return newLatestNews(state, action);
    case DELETE_LATEST_NEWS:
      return deleteLatestNews(state, action);
    case UPDATE_LATEST_NEWS:
      return updateLatestNews(state, action);
    case LOAD_MORE_LATEST_NEWS:
      return loadMoreLatestNews(state, action);
    default:
      return state;
  }
}