import { CHANGE_MENU, CHANGE_CONFIRMATION_SHOWN } from '../actions/types';

export const changeMenu = (menuID) => dispatch => {

  dispatch({
    type: CHANGE_MENU,
    payload: menuID,
  });
};

export const changeConfirmationShown = ({ isShown, description, onYes, onNo }) => dispatch => {

  dispatch({
    type: CHANGE_CONFIRMATION_SHOWN,
    payload: {
      isShown: isShown,
      description: description,
      onYes: onYes,
      onNo: onNo,
    },
  })
};